:root {
  --font-family: "Roboto", sans-serif;
  --primary-color: #00acdc;
  --text-color: #000;
  --secondary-color: #f6f6f7;
}

body {
  margin: 0;
}

.logo {
  display: flex;
}

.menu {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  font-weight: 500;
}

.nav__list {
  font-family: var(--font-family);
  text-align: center;
  flex-wrap: wrap;
  max-width: 690px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: var(--text-color);
  align-items: center;
  height: 60px;
  margin-right: 20px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  font-family: var(--font-family);
  color: var(--primary-color);
  font-weight: 500;
}

.is-active {
  color: var(--primary-color);
  font-weight: 500;
}

.is-active:after {
  content: "";
  background-color: var(--primary-color);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.video {
  margin: 0;
}

